import Navbar from '../components/Navbar';
import HeaderSlider from '../components/HeaderSlider';
import { SliderData } from '../components/HeaderSliderData';
import VerticalTextOnTop from '../components/VerticalTextOnTop';

const Header = () => {
  return (
    <header>
      <Navbar items={['stories', 'events', 'places', 'boards']} />
      <VerticalTextOnTop />
      <HeaderSlider slides={SliderData} />
    </header>
  );
};

export default Header;
