import './Footer.css';
import { ReactComponent as Logo } from '../images/logo.svg';

const Footer = () => {
  return (
    <div className='footer'>
      <div className='footer-logo'>
        <button>
          <Logo />
        </button>
      </div>
      <div>
        <ul className='footer-buttons'>
          <li>
            <button className='small-bold-font' href='#'>
              STORIES
            </button>
          </li>
          <li>
            <button className='small-bold-font' href='#'>
              EVENTS
            </button>
          </li>
          <li>
            <button className='small-bold-font' href='#'>
              PLACES
            </button>
          </li>
          <li>
            <button className='small-bold-font' href='#'>
              BOARDS
            </button>
          </li>
        </ul>
      </div>
      <div className='footer-social-icons'>
        <img src='/images/tumblr.png' alt='' />
        <img src='/images/twitter.png' alt='' />
        <img src='/images/vimeo.png' alt='' />
      </div>
    </div>
  );
};

export default Footer;
