import { surfboards } from '../data/surfboards';

import { Swiper, SwiperSlide } from 'swiper/react';

import './ShopSlider.css';

import 'swiper/swiper.min.css';
import 'swiper/components/navigation/navigation.min.css';
import 'swiper/components/pagination/pagination.min.css';

import SwiperCore, { Navigation } from 'swiper';
import SurfboardCard from '../components/SurfboardCard';
import Button from '../components/buttons/Button';

SwiperCore.use([Navigation]);

export default function ShopSlider() {
  return (
    <section className='shop-slider'>
      <img className='beach-bg' src='/images/beach.png' alt='' />
      <h5 className='small-bold-font blue-font'>SHOP</h5>
      <h2 className='semi-big-bold-font'>Surfboards</h2>
      <Swiper
        slidesPerView={3}
        spaceBetween={0}
        slidesPerGroup={3}
        loop={true}
        loopFillGroupWithBlank={true}
        navigation={true}
        className='mySwiper'
      >
        {surfboards.map((surfboard) => (
          <SwiperSlide key={surfboard.name}>
            <SurfboardCard
              title={surfboard.title}
              image={surfboard.image}
              name={surfboard.name}
              price={surfboard.price}
            />
          </SwiperSlide>
        ))}
      </Swiper>
      <div className='show-all-button'>
        <Button children='SHOW ALL' />
      </div>
    </section>
  );
}
