import './QuoteSection.css';
import VerticalLine from './VerticalLine';

const QuoteSection = ({ text, name }) => {
  return (
    <section className='quote-section'>
      <h2 className='regular-medium-font'>{text}</h2>
      <h5 className='quote-name small-bold-font'>{name}</h5>
      <VerticalLine />
    </section>
  );
};

export default QuoteSection;
