import Button from '../components/buttons/Button';
import './VideoSection.css';

const VideoSection = () => {
  return (
    <div className='video-section'>
      <img className='video-section-photo' src='/images/video-image.jpg' />
      <img className='video-section-video' src='/images/video.jpg' />
      <div className='video-section-text'>
        <p className='gray-small-font'>
          By better understanding the various aspects of surfing, you will
          improve faster and have more fun in the water.
        </p>
        <Button>READ MORE</Button>
      </div>
    </div>
  );
};

export default VideoSection;
