import "./Navbar.css";
import NavbarItem from "./NavbarItem";
import { ReactComponent as Logo } from "../images/logo.svg";

const Navbar = ({ items }) => {
  return (
    <nav>
      <button>
        <Logo />
      </button>
      <ul>
        {items.map((name) => (
          <NavbarItem name={name} />
        ))}
      </ul>
    </nav>
  );
};

export default Navbar;
