import './ClubSectionFirst.css';
import Button from './buttons/Button';
import HorizontalLine from './HorizontalLine';

const ClubSectionFirst = () => {
  return (
    <div className='club-section-first'>
      <div className='club-section-first-text'>
        <h3 className='big-number-font'>01</h3>
        <h2 className='semi-big-bold-font'>Surf Training</h2>
        <HorizontalLine />
        <p className='gray-small-font'>
          By better understanding the various aspects of surfing, By better
          understanding the various aspects of surfing, you will improve faster
          and have more fun in the water.
        </p>
        <Button children='READ MORE' />
      </div>
      <div className='club-section-first-img'>
        <img src='/images/surfing.jpg' alt='' />
      </div>
      <div className='club-section-vertical-text small-bold-font gray-font'>
        Surf Camps
      </div>
    </div>
  );
};

export default ClubSectionFirst;
