import './SurfboardCard.css';

const SurfboardCard = ({ image, title, name, price }) => {
  return (
    <div className='board-background'>
      <img className='board-image' src={image} />
      <div className='board-detail'>
        <h6 className='gray-small-title-font'>{title}</h6>
        <h3 className='regular-medium-font'>{name}</h3>
        <div className='board-price'>
          <h4 className='medium-pink-font'>${price}</h4>
          <h5 className='small-bold-font'>Buy</h5>
        </div>
      </div>
    </div>
  );
};

export default SurfboardCard;
