import './JoinTheClub.css';

const JoinTheClub = () => {
  return (
    <div className='join-the-club'>
      <img src='/images/join-the-club-bg.png' alt='' />
      <h2 className='semi-big-bold-font'>Join the Club</h2>
      <p className='gray-small-font'>
        By better understanding the various aspects of surfing, you will improve
        faster and have more fun in the water.
      </p>

      <form>
        <label htmlFor='email' />
        <input
          className='join-the-club-text-input small-bold-font'
          type='email'
          placeholder='YOUR E-MAIL'
        />
        <input className='join-the-club-submit' type='submit' value='>' />
      </form>
    </div>
  );
};

export default JoinTheClub;
