import './OurCamp.css';
import Button from './buttons/Button';

const OurCamp = () => {
  return (
    <div className='our-camp'>
      <div className='our-camp-img'>
        <img src='/images/our-camp-img.jpg' alt='' />
      </div>
      <div className='our-camp-text'>
        <h5 className='small-bold-font gray-font'>OUR CAMP</h5>
        <h2 className='bold-medium-font'>
          CA 91932, USA
          <br />
          Imperial Beach
          <br />
          560 Silver Strand Blvd
        </h2>
        <Button color='blue' children='GET IN TOUCH' />
      </div>
    </div>
  );
};

export default OurCamp;
