import React, { useState } from 'react';
import './HeaderSlide.css';
import { SliderData } from './HeaderSliderData';
import { BsChevronRight, BsChevronLeft } from 'react-icons/bs';

const HeaderSlider = ({ slides }) => {
  const [current, setCurrent] = useState(1);
  const length = slides.length;

  const nextSlide = () => {
    setCurrent(current === length ? 1 : current + 1);
  };

  const prevSlide = () => {
    setCurrent(current === 1 ? length : current - 1);
  };

  if (!Array.isArray(slides) || slides.length <= 0) {
    return null;
  }

  return (
    <section className='header-slider'>
      {SliderData.map((slide) => {
        return (
          <div
            className={slide.id === current ? 'slide active' : 'slide'}
            key={slide.id}
          >
            {slide.id === current && (
              <div className='slider'>
                <div className='text-area'>
                  <h5 className='small-bold-font blue-font'>
                    {slide.pretitle}
                  </h5>
                  <h2 className='big-bold-font'>{slide.title}</h2>
                  <p className='gray-small-font'>{slide.description}</p>
                  <div className='pages-number'>
                    <h4 className='pages small-bold-font gray-font'>
                      <BsChevronLeft
                        className='arrow gray-font'
                        onClick={prevSlide}
                      />
                      <span className='blue-font'>{slide.id}</span>/
                      <span className='black-font'>{slides.length}</span>
                      <BsChevronRight className='arrow' onClick={nextSlide} />
                    </h4>
                  </div>
                </div>
                <div className='image-area'>
                  <img src={slide.image} alt='travel image' />
                </div>
              </div>
            )}
          </div>
        );
      })}
    </section>
  );
};

export default HeaderSlider;
